<template>
  <ul class="timeline-split">
    <li
      v-for="(post, index) in timeAndPosts"
      :key="index"
      class="timeline-item"
      :class="{'period':post.type=='date'}"
    >
      <div class="timeline-info">
        <span v-if="post.type=='post'">{{post.date}}</span>
      </div>
      <div class="timeline-marker"></div>
      <div class="timeline-content">
        <div v-if="post.id">
          <a :href="'/post/'+post.id">
            <h3 class="timeline-title">{{post.title}}</h3>
          </a>
          <p>{{post.content}}</p>
        </div>
        <h3 v-else class="timeline-title">{{post.title}}</h3>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: ['posts'],
  computed: {
    timeAndPosts: function () {
      const ret = []
      let nowTime = ''
      this.posts.forEach((el, id) => {
        const year = el.date.split(',')[1]
        const month = el.date.split(',')[0].split(' ')[0]
        const date = (month + ' ' + year)
          .toLowerCase()
          .replace(/( |^)[a-z]/g, L => L.toUpperCase())
        if (nowTime !== date) {
          ret.push({ title: date, type: 'date' })
          nowTime = date
        }
        ret.push(Object.assign(el, { type: 'post' }))
      })
      console.log(ret)
      return ret
    }
  }
}
</script>

<style scoped lang="scss">
/*==================================
    TIMELINE
==================================*/
/*-- GENERAL STYLES
    ------------------------------*/
.timeline {
  line-height: 1.4em;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  cursor: pointer;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    line-height: inherit;
  }
}

/*----- TIMELINE ITEM -----*/

.timeline-item {
  padding-left: 40px;
  position: relative;

  &:last-child {
    padding-bottom: 0;
  }
}

/*----- TIMELINE INFO -----*/

.timeline-info {
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 3px;
  margin: 0 0 0.5em 0;
  text-transform: uppercase;
  white-space: nowrap;
}

/*----- TIMELINE MARKER -----*/

.timeline-marker {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 15px;

  &:before {
    background: theme-color("primary");
    border: 3px solid transparent;
    border-radius: 100%;
    content: "";
    display: block;
    height: 15px;
    position: absolute;
    top: 4px;
    left: 0;
    width: 15px;
    transition: background 0.3s ease-in-out, border 0.3s ease-in-out;
  }

  &:after {
    content: "";
    width: 3px;
    background: #ccd5db;
    display: block;
    position: absolute;
    top: 24px;
    bottom: 0;
    left: 6px;
  }

  .timeline-item:last-child &:after {
    content: none;
  }
}

.timeline-item:not(.period):hover .timeline-marker:before {
  background: transparent;
  border: 3px solid theme-color("primary");
}

/*----- TIMELINE CONTENT -----*/

.timeline-content {
  padding-bottom: 2.5rem;

  p:last-child {
    margin-bottom: 0;
  }
}

/*----- TIMELINE PERIOD -----*/

.period {
  padding: 0;
  cursor: default;

  .timeline-info {
    display: none;
  }

  .timeline-marker {
    &:before {
      background: transparent;
      content: "";
      width: 15px;
      height: auto;
      border: none;
      border-radius: 0;
      top: 0;
      bottom: 30px;
      position: absolute;
      border-top: 3px solid #ccd5db;
      border-bottom: 3px solid #ccd5db;
    }

    &:after {
      content: "";
      height: 32px;
      top: auto;
    }
  }

  .timeline-content {
    padding: 40px 0 70px;
  }

  .timeline-title {
    margin: 0;
  }
}

/*----------------------------------------------
        MOD: TIMELINE SPLIT
    ----------------------------------------------*/

.timeline-split {
  li {
    list-style-type: none;
  }
  @media (min-width: 992px) {
    .timeline {
      display: table;
    }

    .timeline-item {
      display: table-row;
      padding: 0;
    }

    .timeline-info,
    .timeline-marker,
    .timeline-content,
    .period .timeline-info {
      display: table-cell;
      vertical-align: top;
    }

    .timeline-marker {
      position: relative;
    }

    .timeline-content {
      padding-left: 30px;
    }

    .timeline-info {
      padding-right: 30px;
    }

    .period .timeline-title {
      position: relative;
      left: -45px;
    }
  }
}

/*----------------------------------------------
        MOD: TIMELINE CENTERED
    ----------------------------------------------*/

.timeline-centered {
  @extend .timeline-split;

  @media (min-width: 992px) {
    &,
    .timeline-item,
    .timeline-info,
    .timeline-marker,
    .timeline-content {
      display: block;
      margin: 0;
      padding: 0;
    }

    .timeline-item {
      padding-bottom: 40px;
      overflow: hidden;
    }

    .timeline-marker {
      position: absolute;
      left: 50%;
      margin-left: -7.5px;
    }

    .timeline-info,
    .timeline-content {
      width: 50%;
    }

    > .timeline-item:nth-child(odd) .timeline-info {
      float: left;
      text-align: right;
      padding-right: 30px;
    }

    > .timeline-item:nth-child(odd) .timeline-content {
      float: right;
      text-align: left;
      padding-left: 30px;
    }

    > .timeline-item:nth-child(even) .timeline-info {
      float: right;
      text-align: left;
      padding-left: 30px;
    }

    > .timeline-item:nth-child(even) .timeline-content {
      float: left;
      text-align: right;
      padding-right: 30px;
    }

    > .timeline-item.period .timeline-content {
      float: none;
      padding: 0;
      width: 100%;
      text-align: center;
    }

    .timeline-item.period {
      padding: 50px 0 90px;
    }

    .period .timeline-marker:after {
      height: 30px;
      bottom: 0;
      top: auto;
    }

    .period .timeline-title {
      left: auto;
    }
  }
}

/*----------------------------------------------
        MOD: MARKER OUTLINE
    ----------------------------------------------*/

.marker-outline {
  .timeline-marker {
    &:before {
      background: transparent;
      border-color: theme-color("primary");
    }
  }

  .timeline-item:hover .timeline-marker:before {
    background: theme-color("primary");
  }
}
</style>
